import { Link } from 'react-router-dom';
import InputWithError from '../../../../animations/input-error-field';
import { useState, useEffect } from "react";

const CodePhase = ({phase, setPhase, code, setCode, emailCode, email}) =>{
    const [error, setError] = useState(false);
    const [warning, setWarning] = useState(false);

    useEffect(()=>{
        try {
            if(code != ''){
                throw Error(' ')
            }
        } catch (error) {
            if(error.message == ' '){
                setWarning(true);
            }else if(error.message == ' '){
                setError(true);
            }
        }
        
    },[code])

    const click = () =>{
        if(emailCode == code){
            setError(false);
            setPhase(phase + 1);
        }else{
            setError(true);
        }
    };

    return(<div className="phases-wrapper">
        <div className="sighnInUp-annotation">
            <h1>   </h1>
            <p>    {email}</p>
        </div>
        <div style={{display:'flex', flexDirection: 'column'}}>
            <button className='code-timer-button'>1:30</button>
            <Link className='link-black'> </Link>
        </div>
        <div className="sighnInUp-input-with-text">
            <InputWithError placeholder={''} value={code} setValue={setCode} 
                error={error} setError={setError} errorText={' '}
                warning={warning} setWarning={setWarning} warningText={' '}
                inputType='number'></InputWithError>
        </div>
        <button className="continue-button" onClick={click}></button>
        <Link className='link-black'>  </Link>
    </div>);
}

export default CodePhase;